/* Banner for Matthew's official websites */

:root {
	--mh-theme-primary: black;
	--mh-theme-secondary: whitesmoke;
	--mh-theme-inactive: dimgray;
	--mh-theme-active: darkgray;
	--mh-theme-duration: 0.22;
	--mh-theme-drop-off: 0.75;
	--mh-theme-ease: linear(
		0 0%,
		0.2342 12.49%,
		0.4374 24.99%,
		0.6093 37.49%,
		0.6835 43.74%,
		0.7499 49.99%,
		0.8086 56.25%,
		0.8593 62.5%,
		0.9023 68.75%,
		0.9375 75%,
		0.9648 81.25%,
		0.9844 87.5%,
		0.9961 93.75%,
		1 100%
	);
	--mh-theme-border: gray;
	--mh-banner-background-color: var(--banner-background-color, white);
	--mh-banner-color: var(--banner-color, #aaa);
	/*--mh-banner-content-color: var(--banner-content-color, #aaa);*/
	--mh-banner-hover-color: var(--banner-hover-color, white);
	--mh-banner-link-color: var(--banner-link-color, lightblue);

	color-scheme: light dark;

	&[data-theme='light'] {
		color-scheme: light;

		.mh-banner-theme:empty::before {
			content: '\2600';
		}
	}

	&[data-theme='dark'] {
		color-scheme: dark;

		.mh-banner-theme:empty::before {
			content: '\263D';
		}
	}
}

.matthews-banner {
	position: relative;
	display: flex;
	text-align: center;
	justify-content: center;
	align-items: center;
	width: 100%;
	margin: 0;
	padding: 0;
	background-color: inherit;
	color: inherit;
	z-index: 400;
}

.mh-banner {
	position: sticky;
	top: 0;
	left: 0;
	background-color: inherit;
	color: var(--mh-banner-color);
	cursor: pointer;
	width: 100%;
	border: none;
	text-align: center;
	outline: none;
	font-size: 10px;
}

.banner-active,
.mh-banner:hover {
	filter: brightness(1.5) contrast(1.5) sepia(0.5);
}

.mh-banner:after {
	content: '\25BC';
	margin-left: 5px;
}

.banner-active:after {
	content: '\25B2';
}

.mh-banner-content {
	display: grid;
	position: fixed;
	top: 0;
	left: 0;
	grid-template-columns: minmax(0, 1fr) auto auto auto minmax(0, 1fr);
	gap: 5px;
	overflow: hidden;
	font-size: 12px;
	transition: max-height 0.5s ease-out;
	background-color: var(--mh-banner-background-color);
	color: var(--mh-banner-color);
	opacity: 1;
	width: 100%;
	max-height: 0;

	& ul {
		margin: 0;
		padding: 0;
		list-style-type: none;
	}

	& hr {
		height: 6px;
		border: none;
		background: linear-gradient(
			to right,
			var(--mh-banner-background-color),
			var(--mh-banner-color)
		);
		color: var(--mh-banner-color);
	}

	& a {
		color: var(--mh-banner-link-color);
		text-decoration: none;
	}

	& a:hover {
		filter: brightness(120%);
		text-decoration: underline;
	}

	& div.mh-banner-content-center {
		padding-top: 18px;
		grid-column: 1 / span 5;
		text-align: center;
		align-items: center;
		justify-items: center;
	}

	& .mh-banner-content-col2,
	.mh-banner-content-col4 {
		text-align: left;
		align-items: start;
		justify-items: start;
	}
}

.mh-banner-theme {
	position: absolute;
	top: 0;
	left: 10%;
	background-color: inherit;
	color: var(--mh-banner-color);
	cursor: pointer;
	width: 1em;
	border: none;
	text-align: center;
	outline: none;
	font-size: 24px;
}

.mh-banner-theme-right {
	left: 90%;
}

.mh-banner-theme:empty::before {
	content: '\24C8';
}

dialog.mh-banner-theme-dialog {
	background-color: white;
	color: black;
	border: 2px solid black;
	font-family: serif, sans-serif;
	font-size: 16px;
	padding-top: 30px;

	&.dialog-left {
		margin-left: 50px;
	}

	&.dialog-right {
		margin-right: 50px;
	}

	& fieldset {
		margin-bottom: 0.25em;
	}

	& select {
		background-color: var(--mh-theme-secondary);
		color: black;
		width: 100%;

		& option {
			border: 1px solid black;
			cursor: pointer;

			&:hover {
				background-color: var(--mh-theme-inactive);
			}

			&:checked {
				background-color: var(--mh-theme-active);
			}
		}
	}

	& button {
		background-color: var(--mh-theme-secondary);
		color: black;
		cursor: pointer;

		&:hover {
			filter: brightness(85%);
		}

		& .cancel {
			float: left;
		}

		& .apply {
			float: right;
		}
	}

	& div.button-box {
		display: flex;
	}

	& div.button-box-action {
		justify-content: space-between;
		align-items: center;
	}
}

.dialog-toggle {
	display: grid;
	width: 300px;
	height: 38px;
	padding: 2px;
	aspect-ratio: 300 / 38;
	background: var(--mh-theme-secondary);
	border-radius: 100px;
	border: 1px solid var(--mh-theme-border);
	font-size: 16px;
	letter-spacing: 0.5px;

	& label {
		display: grid;
		place-items: center;
		height: 100%;
		width: 100%;
		color: var(--mh-theme-primary);
		font-size: 0.75em;
		z-index: 400;
		cursor: pointer;
		transition-property: color, opacity;
		transition-duration: calc(var(--mh-theme-duration) * 1s);
		transition-timing-function: var(--mh-theme-ease);
	}

	& label span {
		display: grid;
		place-items: center;
		height: 100%;
		width: 100%;
		transition: scale calc(var(--mh-theme-duration) * 1s) var(--mh-theme-ease);
	}

	& .dialog-toggle-border {
		display: grid;
		place-items: center;
		grid-template-columns: repeat(4, 1fr);
		width: 100%;
		height: 100%;
		position: relative;
	}

	& .dialog-toggle-border > label {
		color: var(--mh-theme-secondary);
	}

	& .active {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 50%;
		background: var(--mh-theme-primary);
		border-radius: 100px;
		transition: translate calc(var(--mh-theme-duration) * 1s)
			var(--mh-theme-ease);
	}

	& .theme {
		width: 100%;
		height: 100%;
		display: grid;
		position: relative;
		grid-template-columns: 1fr 1fr;
		container-type: size;
	}

	& .theme,
	.dialog-toggle-border > label:nth-of-type(1) {
		grid-column: span 2;
	}

	& .theme label:nth-of-type(1) span {
		scale: 0.75;
		transform-origin: 150% 150%;
		border-radius: 100px;
	}

	& .theme label:nth-of-type(2) span {
		scale: 0.75;
		transform-origin: -65% 150%;
		border-radius: 100px;
	}

	& .theme .active {
		background: var(--mh-theme-primary);
		top: 0;
		left: 50%;
		translate: -50% 0;
	}

	& .theme::before {
		content: 'Theme';
		position: absolute;
		top: 50%;
		left: 50%;
		translate: -50% -80%;
		color: var(--mh-theme-primary);
		font-size: 0.75em;
	}

	& .theme:has(:checked)::before {
		translate: -50% -200%;
		scale: 0.85;
	}

	& .theme:has(:checked) label span {
		scale: 1;
	}

	& .theme:has(:checked) .active {
		background: var(--mh-theme-secondary);
		clip-path: inset(10cqh 10cqh 10cqh 10cqh round 100px);
	}

	& .theme:has(:checked) label {
		color: var(--mh-theme-inactive);
		opacity: var(--mh-theme-drop-off);
		filter: brightness(250%);
	}

	& .theme:has(:nth-of-type(1):checked) label:nth-of-type(1),
	.theme:has(:nth-of-type(2):checked) label:nth-of-type(2) {
		color: var(--mh-theme-primary);
		opacity: 1;
	}

	& .theme:has(:nth-of-type(1):checked) .active {
		translate: -100% 0;
	}

	& .theme:has(:nth-of-type(2):checked) .active {
		translate: 0 0;
	}

	& .theme .active {
		transition-property: translate, clip-path, background;
		transition-duration: calc(var(--mh-theme-duration) * 1s);
		transition-timing-function: var(--mh-theme-ease);
		clip-path: inset(
			73cqh calc(50% + 1px) calc(27cqh - 2px) calc(50% - 3px) round 100px
		);
	}

	& .theme::before {
		transition-property: scale, translate;
		transition-duration: calc(var(--mh-theme-duration) * 1s);
		transition-timing-function: var(--mh-theme-ease);
	}

	& .theme-options {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		white-space: nowrap;
		border-width: 0;
	}

	& .dialog-toggle-border:not(:has(> :checked)) > label {
		color: var(--mh-theme-primary);
		opacity: var(--mh-theme-drop-off);
	}

	& .dialog-toggle-border:not(:has(> :checked)) > .active {
		translate: 100% 0;
	}

	& .dialog-toggle-border:has(> :checked) .theme .active {
		background: var(--mh-theme-inactive);
	}

	& .dialog-toggle-border:has(> :checked) .theme label {
		color: var(--mh-theme-inactive);
		filter: brightness(50%);
	}
}

